.exerciseDetail{
    background-color: #edfaf8;
    overflow: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding:10px 100px;
    margin:20px;
}

.exerciseButtons{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    margin-bottom:20px;
}

.alignLeft{
    text-align: left;
}

@media only screen and (max-width: 600px){
    .exerciseDetail{
        margin:2px;
        padding: 10px 3px;
    }
}


